var string = "<% var imgURL = model.get('headshot') %>\n<img id='avatar' height='100' src=\"<%= imgURL || '/vendor/img/avatar.svg' %>\">\n\n<table>\n  <tr>\n    <th>Name:</th>\n    <td><span id='first_name'><%= model.get('first_name') %></span> <span id='last_name'><%=model.get('last_name') %></span></td>\n  </tr>\n  <tr>\n    <th>Username:</th>\n    <td><%= model.get('username') %></td>\n  </tr>\n  <tr>\n    <th>E-mail:</th>\n    <td><%= model.get('email') %></td>\n  </tr>\n  <tr>\n    <th>Birthdate:</th>\n    <td><%= model.get('birthdate') || 'N/A' %></td>\n  </tr>\n  <tr>\n    <th>Fach:</th>\n    <td><%= model.get('fach') || 'N/A' %></td>\n  </tr>\n  <tr>\n    <th>City:</th>\n    <td><%= model.get('city') || 'N/A' %></td>\n  </tr>\n  <tr>\n    <th>Member Since:</th>\n    <td><%= model.get('created_at') %></td>\n  </tr>\n</table>";

module.exports = string;
